// Helper function to get player stats
export const getPlayerStats = (playerName) => {
  if (!playerName) return null;
  
  // Try exact match first
  const exactMatch = playerStats[playerName];
  if (exactMatch) return exactMatch;
  
  // Try case-insensitive match
  const nameLower = playerName.toLowerCase().trim();
  const matchingKey = Object.keys(playerStats).find(
    key => key.toLowerCase().trim() === nameLower
  );
  
  return matchingKey ? playerStats[matchingKey] : null;
};

